@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    font-family: 'IBM Plex Sans', sans-serif;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}


.App {
  text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-float infinite 3s ease-in-out;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}

@keyframes App-logo-float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}

.emoji-form{position:relative}.emoji-form .emoji-input-smile{position:absolute;bottom:10px;right:10px}.emoji-form .emoji-input-span{resize:none;height:100%;padding:9px 34px 10px 13px;font-size:13px;outline:none;display:block;min-height:40px;border:2px solid #dae2ea;border-radius:8px;text-align:left}
.emoji-container{border-radius:2px;height:300px;position:absolute;top:-20px;transform:translateY(-100%);z-index:1000;opacity:1;transition:opacity .2s}.emoji-container.hidden{opacity:0;z-index:-1000}.emoji-container .emoji-picker{border:2px solid #dae2ea;height:262px;overflow-y:scroll}.emoji-last{height:36px;display:flex;flex-direction:row;justify-content:flex-start;align-items:center;background:#dae2ea;border-bottom-right-radius:2px;border-bottom-left-radius:2px;position:relative}.emoji-last *{box-sizing:content-box;padding:10px 10px;background-clip:padding-box}.emoji-last .smile-last,.emoji-last .recent-last{width:18px;height:18px}.emoji-last.all>.smile-last,.emoji-last.recent>.recent-last{background:#fff}
section.emoji-section{position:relative;top:5%}section.emoji-section p{text-align:left;margin:0 0 5px 8px;opacity:.6;font-size:20px}section.emoji-section .emoji-grid{width:100%;height:80%;display:grid;grid-template-columns:repeat(10, 1fr);grid-gap:8px;gap:8px;padding:0 8px}
